<template>
	<div id="app">
		<nav class="navbar navbar-expand-lg navbar-dark bg-dark">
			<a class="navbar-brand" href="#">EatCats</a>

			<ul class="navbar-nav ml-auto">   
				<li class="nav-item active">
					<router-link class="nav-link" to="/">Main</router-link>
				</li>    
				<li class="nav-item active">
					<router-link class="nav-link" to="/other">Other</router-link>
				</li>      
				<li class="nav-item active">
					<a class="nav-link" href="#">Регестрація</a>
				</li>
				<li class="nav-item">
					<button class="btn btn-outline-light" data-toggle="modal" data-target="#login-modal" href="#">Вхід</button>
				</li>
			</ul>
		</nav>
		<h1>{{ isLogin }}</h1>
		<router-view/>

		<div class="modal" id="login-modal" tabindex="-1" role="dialog">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
					<div class="modal-header">
						<h5 class="modal-title">Вхід</h5>
						<button type="button" class="close" data-dismiss="modal" aria-label="Close">
							<span aria-hidden="true">&times;</span>
						</button>
					</div>
					<div class="modal-body">
						<form action="/" method="Post" id="login-form">
							<div class="form-group row">
								<label for="login" class="col-sm-2 col-form-label">Логін</label>
								<div class="col-sm-10">
									<input type="text" class="form-control" id="login" v-model="login" placeholder="Логін">
								</div>
							</div>
							<div class="form-group row">
								<label for="inputPassword" class="col-sm-2 col-form-label">Пароль</label>
								<div class="col-sm-10">
									<input type="password" class="form-control" id="inputPassword" v-model="password" placeholder="Пароль">
								</div>
							</div>
						</form>
					</div>
					<div class="modal-footer">
						<button type="submit" id="submit-form" class="btn btn-primary" @click="submit">Ввійти</button>
						<button type="button" class="btn btn-secondary" data-dismiss="modal">Закрити</button>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'app',
	data () {
		return {
			title: 'Якісне управління для кота',
			isLogin: false,
			login: '',
			password: ''
		}
	},
	methods: {
		submit: function(event) {
			let data = {
				login: this.login,
				pass: this.password
			}

		 this.$http.post('http://localhost:8000/users', data,).then(response => {
		    this.isLogin = true
		  }, err => {
		  	console.log(err); 
		  });
		}
	}

}
</script>

<style>

</style>



